import React, { Component } from "react";
import { createAppContainer } from "react-navigation";

import { createStackNavigator } from "react-navigation-stack";
import { createDrawerNavigator } from "react-navigation-drawer";

import { createBottomTabNavigator } from "react-navigation-tabs";
import {
  Foundation,
  MaterialCommunityIcons,
  AntDesign,
  FontAwesome,
  Entypo,
} from "@expo/vector-icons";

import {
  HomeScreen,
  AboutScreen,
  ProfileScreen,
  ChatScreen,
  SearchScreen,
  DetailScreen,
} from "../Screens";
import { dH, dW } from "../Constants/Responsive";
import { Text, View, ImageBackground } from "react-native";
import CustomDrawerComponent from "./DrawerComponent";
import Colors from "../Constants/Colors";
import { Constants } from "expo";

import { Images } from "../Constants";

const HomeStack = createStackNavigator(
  {
    LandingPage: { screen: HomeScreen },
    Detail: { screen: DetailScreen },
    About: { screen: AboutScreen },
  },
  {
    headerMode: "none",
    initialRouteName: "LandingPage",
  }
);

const TabNavigator = createBottomTabNavigator(
  {
    Menu: {
      screen: HomeScreen,
      navigationOptions: {
        tabBarOnPress: ({ navigation }) => navigation.openDrawer(),
        // tabBarLabel: 'Home',
        tabBarIcon: ({ focused, tintColor }) => (
          <Entypo name="menu" size={true ? dH(13) : dH(24)} color="#fff" />
        ),
      },
    },
    Home: {
      screen: HomeStack,
      navigationOptions: {
        // tabBarLabel: 'Home',
        tabBarIcon: ({ focused, tintColor }) => (
          <Foundation
            color={"#fff"}
            name="home"
            size={true ? dH(13) : dH(24)}
          ></Foundation>
        ),
      },
    },
    Search: {
      screen: SearchScreen,
      navigationOptions: {
        // tabBarLabel: 'Home',
        tabBarIcon: ({ focused, tintColor }) => (
          <FontAwesome
            color={"#fff"}
            name="search"
            size={true ? dH(13) : dH(24)}
          ></FontAwesome>
        ),
      },
    },
    Chat: {
      screen: ChatScreen,
      navigationOptions: {
        // tabBarLabel: 'Home',
        tabBarIcon: ({ focused, tintColor }) => (
          <MaterialCommunityIcons
            color={"#fff"}
            name="facebook-messenger"
            size={true ? dH(13) : dH(24)}
          ></MaterialCommunityIcons>
        ),
      },
    },
    MyStuff: {
      screen: ProfileScreen,
      navigationOptions: {
        tabBarLabel: "My Stuff",
        tabBarIcon: ({ focused, tintColor }) => (
          <FontAwesome
            color={"#fff"}
            name="user"
            size={true ? dH(13) : dH(24)}
          ></FontAwesome>
        ),
      },
    },
  },
  {
    initialRouteName: "Home",
    lazy: true,
    animationEnabled: false,
    tabBarOptions: {
      activeTintColor: Colors.primary,
      inactiveTintColor: Colors.tabBarIcon_C1C0C9,
      upperCaseLabel: false,
      showLabel:false,
      style: {
        backgroundColor: Colors.secondary,
        justifyContent: "center",
        alignItems: "center",
      },
      labelStyle: {
        fontSize: dH(10),
      },
      safeAreaInset: { bottom: "never", top: "never" },
      tabStyle: {
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: Colors.four,
      },
    },
  }
);

const Stack = createStackNavigator(
  {
    Tab: { screen: TabNavigator },
    Search: { screen: SearchScreen },
    // LandingPage: { screen: HomeScreen },
    // Login: { screen: LoginScreen },
    // Register: { screen: RegisterScreen },
    // VerifyUser,
    // ForgotPassword,
    // ChangePassword,
    // ContactUs,
  },
  {
    headerMode: "none",
    initialRouteName: "Tab",
  }
);

const MainNavigator = createDrawerNavigator(
  {
    Main: { screen: Stack },
  },
  {
    drawerBackgroundColor: "#fff",
    contentComponent: CustomDrawerComponent,
    drawerWidth: "50%",
  }
);

// const MainNavigator = TabNavigator;

export default createAppContainer(MainNavigator);
