import React, { Component } from "react";
import { View, TextInput, Text, TouchableOpacity } from "react-native";
import Colors from "../Constants/Colors";
import { Ionicons, FontAwesome } from "@expo/vector-icons";
import { dH,dW } from "../Constants";

export const SearchBar = ({
  nearme = false,
  onNearMePressed = () => console.warn("near me pressed"),
  onChangeText = () => console.log("onChangeText"),
  onSubmitEditing = () => console.log("onSubmitEditing"),
  value = "",
  cross = true,
  onPressCross = () => console.warn("asdasd"),
}) => (
  <View
    style={{
      marginVertical: dH(4),
      borderRadius: 6,
      backgroundColor: "#f0f0f0",
      flexDirection: "row",
      paddingHorizontal: dH(8),
      justifyContent: "space-around",
      alignItems: "center",
    }}
  >
    <View
      style={{
        flexDirection: "row",
        flex: 1.5,
        backgroundColor: "#f0f0f0",
        justifyContent: "center",
        alignItems: "center",
        padding: dW(2),
      }}
    >
      <Ionicons
        name="ios-search"
        size={dH(20)}
        color={Colors.searchIcon_707070}
      />
      <TextInput
        onChangeText={onChangeText}
        placeholderTextColor={"#c0c0c0"}
        placeholder="Search"
        returnKeyLabel="Search"
        returnKeyType="search"
        style={{
          flex: 1,
          padding: dW(4),
          paddingHorizontal: dH(16),
          backgroundColor: "#f0f0f0",
          fontSize: dH(15),
          color: "#707070",
        }}
        onSubmitEditing={onSubmitEditing}
        value={value}
      ></TextInput>
    </View>
    {nearme && (
      <TouchableOpacity
        activeOpacity={0.8}
        onPress={onNearMePressed}
        style={{
          flex: 1,
          borderLeftWidth: 1,
          justifyContent: "center",
          alignItems: "center",
          borderColor: Colors.secondaryDark,
          flexDirection: "row",
        }}
      >
        <FontAwesome
          name="location-arrow"
          size={dH(16)}
          color={Colors.secondaryDark}
          style={{ padding: dW(8) }}
        />
        <Text style={{ color: Colors.secondaryDark }}>Near me</Text>
      </TouchableOpacity>
    )}
    {cross && (
      <TouchableOpacity
        activeOpacity={0.8}
        onPress={onPressCross}
        style={{
          justifyContent: "center",
          alignItems: "center",
          borderColor: Colors.secondaryDark,
          flexDirection: "row",
        }}
      >
        <FontAwesome
          name="close"
          size={dH(16)}
          color={Colors.secondaryDark}
          style={{ padding: dW(8) }}
        />
      </TouchableOpacity>
    )}
  </View>
);
