import React, { Component, Fragment } from "react";
import {
  Text,
  View,
  Image,
  ScrollView,
  FlatList,
  ActivityIndicator,
  Dimensions,
  TouchableOpacity,
  ImageBackground,
  Modal,
} from "react-native";
import * as Facebook from "expo-facebook";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Ionicons } from "@expo/vector-icons";
import { getAboutInformation } from "./Action";

import _ from "lodash";
import {
  Loading,
  DoubleCard,
  SingleCard,
  FoxTopBar,
  FoxStandardCard,
  FoxMasonaryCard,
  FoxButton,
  FoxAvatar,
  FBLikeButton,
  UserCard,
} from "../../Components";
import { socialLoginFetchProfile } from "./Action";
import moment from "moment";
import { Foundation, AntDesign, Entypo } from "@expo/vector-icons";
import { Colors, dH,dW } from "../../Constants";
import WebView from "react-native-webview";

class Screen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      offset: 0,
      showmenu: true,
      showLogIn: false,
      webViewHeight: 100,
    };
  }
  isCloseToBottom({ layoutMeasurement, contentOffset, contentSize }) {
    return (
      layoutMeasurement.height + contentOffset.y >= contentSize.height - 20
    );
  }
  isCloseToTop({ layoutMeasurement, contentOffset, contentSize }) {
    return contentOffset.y == 0;
  }
  /**
   * id, date, date_gmt, guid, rendered, modified, modified_gmt, slug, status, type, link, title, rendered, content, rendered, protected, featured_media, comment_status, ping_status, template, _links, self, href, collection, href, about, href, replies, embeddable, href, wp:featuredmedia, embeddable, href, wp:attachment, href, curies, name, href, templated
   */
  onWebViewMessage = (event) => {
    this.setState({ webViewHeight: Number(event.nativeEvent.data) });
  };
  async componentDidMount() {
    await this.props.actions.getAboutInformation({ page_id: 71 });
  }
  render() {
    const { isFetching, response, users } = this.props.about;
    return (
      <Fragment>
        {!_.isEmpty(response) && (
          <ScrollView
            showsVerticalScrollIndicator={false}
            contentContainerStyle={{
              backgroundColor: Colors.two,
            }}
          >
            <WebView
              originWhitelist={["*"]}
              source={{
                html: `<html><head><meta name="viewport" content="width=device-width, initial-scale=1.0"></head><body>${response.content.rendered}</body></html>`,
              }}
              onMessage={this.onWebViewMessage}
              injectedJavaScript="window.ReactNativeWebView.postMessage(document.body.scrollHeight)"
              style={{
                backgroundColor: Colors.one,
                marginTop: 20,
                flex: 1,
                height: this.state.webViewHeight,
              }}
            ></WebView>
            <TouchableOpacity onPress={() => this.props.navigation.goBack()} style={{position:"absolute", padding:dH(8), }}>
                
                <Ionicons name="md-arrow-back" size={24} color={Colors.secondary} />
              </TouchableOpacity>
            <View
              style={{
                justifyContent: "center",
                alignItems: "center",
                borderBottomWidth: 2,
                borderColor: Colors.primary,
                margin: dW(8),
              }}
            >
              <Text
                style={{
                  fontSize: dH(30),
                  textAlign: "center",
                  textTransform: "uppercase",
                  letterSpacing: 10,
                  paddingVertical: dH(8),
                  fontWeight: "bold",
                  color: Colors.primary,
                }}
              >
                Our Team
              </Text>
            </View>
            <View>
              <FlatList
                keyExtractor={(item) => item.index}
                horizontal
                data={users}
                renderItem={({ item }) => {
                  const imgUrlSeperator = item.description.split("|||");
                  const imgUrl = _.isEmpty(imgUrlSeperator)
                    ? "https://img.icons8.com/ios/2x/user.png"
                    : imgUrlSeperator[1];

                  return (
                    <View
                      style={{
                        width: Dimensions.get("screen").width / 4,
                        paddingHorizontal: dH(4),
                      }}
                    >
                      <UserCard
                        name={item.name}
                        imgUrl={imgUrl}
                        role={item.roles[0]}
                      ></UserCard>
                    </View>
                  );
                }}
              ></FlatList>
            </View>
          </ScrollView>
        )}
        {/*
        {this.state.showmenu && (
          <FoxTopBar
            onPressMenu={() => this.props.navigation.openDrawer()}
          ></FoxTopBar>
        )}
        */}
        {isFetching && <Loading></Loading>}
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  about: state.about,
  profile: state.profile,
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators({ getAboutInformation }, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Screen);
