// reducers
import {
  PROFILE_FETCHING_DATA,
  PROFILE_FETCHING_DATA_FAILURE,
  PROFILE_FETCHING_DATA_RESET,
  PROFILE_FETCHING_DATA_SUCCESS,
  PROFILE_FETCHING_DATA_ATTEMPT,
  REMOVE_USER,
  TOKEN_STORE_FB,
  PROFILE_REQUEST_CANCEL,
} from "./Action";
import { removeDuplicates } from "../../Constants";
import _ from "lodash";
import {
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
} from "../Search/Action";
let initialState = {
  page: 0,
  name: "",
  dataInfo: [],
  profileLink: "",
  logInResponse: {
    token: "",
  },
  data: {
    shareData: [],
    groupShareData: [],
    linkShareData: [],
    voteData: [],
    rank: [],
    commentData: [],
  },
  isError: "",
  isFetching: "",
  email: "",
  id: "",
  avatar: "",
};

export default function Reducer(state = initialState, action) {
  switch (action.type) {
    case NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
      };

    case NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
      };
    case REMOVE_USER:
      return {
        ...state,
        dataInfo: state.dataInfo.filter((v) => v !== action.payload),
      };
    case PROFILE_FETCHING_DATA_ATTEMPT:
      return {
        ...state,
        isFetching: true,
        isError: false,
      };
    case PROFILE_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
        isError: action.payload,
      };
    case TOKEN_STORE_FB:
      return {
        ...state,
        logInResponse: {
          token: action.payload,
        },
      };
    case PROFILE_REQUEST_CANCEL:
      return {
        ...state,
        isFetching: false,
        isError: false,
      };
    case PROFILE_FETCHING_DATA_SUCCESS: {
      if (_.isEmpty(state.dataInfo)) {
        return {
          ...state,
          isFetching: false,
          isError: false,
          name: action.payload.name,
          profileLink: action.payload.link,
          email: action.payload.email,
          id: action.payload.id,
          avatar: action.payload.picture,
          dataInfo: [
            {
              email: action.payload.email,
              id: action.payload.id,
              avatar: action.payload.picture,
              name: action.payload.name,
              site: action.payload.site,
            },
          ],
          data: action.data,
        };
      } else {
        const mydata = removeDuplicates(
          (thing1, thing2) => thing1.id === thing2.id,
          [
            ...state.dataInfo,
            {
              email: action.payload.email,
              id: action.payload.id,
              avatar: action.payload.picture,
              name: action.payload.name,
              site: action.payload.site,
            },
          ]
        );
        return {
          ...state,
          isFetching: false,
          isError: false,
          name: action.payload.name,
          profileLink: action.payload.link,
          email: action.payload.email,
          id: action.payload.id,
          avatar: action.payload.picture,
          dataInfo: mydata.reverse(),
          data: action.data,
        };
      }
    }
    case PROFILE_FETCHING_DATA_RESET:
      return initialState;
    default:
      return state;
  }
}
