export const FilterCard = ({
  showModal = () => console.warn("showModal"),
  item,
  main = false,
  changeCategoryData = () => console.warn("changeCategoryData"),
}) => (
  <View>
    <TouchableOpacity
      activeOpacity={0.8}
      style={{
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        marginHorizontal: dW(8),
        marginVertical: dH(8),
      }}
      onPress={() => showModal(item.catname)}
    >
      <View
        style={[
          {
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center",
            borderColor: Colors.cateogryborderText_EDEDED,
          },
          !main && { borderWidth: 1, padding: 4 },
        ]}
      >
        <Text
          style={[
            {
              fontSize: dH(17),
              color: Colors.cateogryborderText_EDEDED,
            },
            main && { fontWeight: "500" },
          ]}
        >
          {item.catname}
        </Text>
        <AntDesign
          name="down"
          color={Colors.cateogryborderText_EDEDED}
          size={dH(12)}
          style={{ paddingHorizontal: 2 }}
        ></AntDesign>
      </View>
    </TouchableOpacity>
    <Modal
      visible={item.status == 1 ? false : true}
      transparent
      animationType="slide"
    >
      <TouchableOpacity
        activeOpacity={0.8}
        onPress={() => showModal(item.catname)}
        style={{
          flex: 1,
          backgroundColor: Colors.secondaryDark,
          opacity: 0.5,
        }}
      ></TouchableOpacity>
      <View
        style={{
          width: "100%",
          backgroundColor: Colors.secondary,
          borderRadius: 10,
          borderWidth: 1,
          borderColor: Colors.secondary,
          padding: dW(16),
        }}
      >
        <View style={{ flexDirection: "row", alignItems: "center" }}>
          <TouchableOpacity
            activeOpacity={0.8}
            onPress={() => showModal(item.catname)}
            style={{ position: "absolute" }}
          >
            <Entypo name="cross" size={dH(30)} color={Colors.secondaryDark} />
          </TouchableOpacity>
          <View
            style={{
              flex: 1,
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Text style={{ fontSize: dH(20) }}>Choose {item.catname}</Text>
          </View>
        </View>
        <View
          style={{
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <FlatList
            contentContainerStyle={{
              flex: 1,
              flexDirection: "row",
              justifyContent: "center",
              flexWrap: "wrap",
            }}
            horizontal
            data={item.childs}
            keyExtractor={(item) => item.index}
            renderItem={(item1) => (
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() =>
                  changeCategoryData(item.catname, item1.item.label)
                }
                style={[
                  Styles.categoryLabel,
                  item1.item.isSelected && Styles.selectedCategoryLabel,
                ]}
              >
                {item1.item.isSelected && (
                  <Entypo
                    name="check"
                    size={dH(17)}
                    color={Colors.secondary}
                  ></Entypo>
                )}
                <Text style={{ fontSize: dH(14), color: Colors.secondary }}>
                  {item1.item.label}
                </Text>
              </TouchableOpacity>
            )}
          />
        </View>
      </View>
    </Modal>
  </View>
);

