export const FoxStandardCard = ({
  item,
  onPress = () => console.log("onPress"),
  userEmail = null,
}) => {
  const title = (item.title.rendered);
  const content = (item.content.rendered);
  return (
    <TouchableOpacity
      activeOpacity={0.8}
      onPress={onPress}
      style={[
        {
          width: "100%",
          borderColor: Colors.primaryDark,
          backgroundColor: Colors.one,
        },
        Styles.shadowGenerator,
        {
          elevation: 2,
          marginBottom: 4,
        },
      ]}
    >
      <View>
        {item.fimg_url && (
          <Image
            style={{ width: "100%", height: dH(200), flex: 1 }}
            source={{
              uri: item.fimg_url,
            }}
            resizeMode="contain"
          />
        )}
      </View>

      <View style={{ flex: 1, padding: dW(8) }}>
        <Text
          style={{
            color: Colors.oneText,
            fontSize: dH(20),
            fontWeight: "bold",
          }}
        >
          {_.truncate(decodeHTMLEntities(title), { length: 70 })}
        </Text>
        <View style={{ flex: 1 }}>
          <Text
            style={{
              color: Colors.twoText,
              height: dH(18) * 3,
              fontSize: dH(14),
            }}
          >
            {_.truncate(
              decodeHTMLEntities(
                content
                  .replace(/(<([^>]+)>)/gi, "")
                  .replace(/(\r\n|\n|\r)/gm, " ")
              ),
              {
                length: 190,
              }
            ).trim()}
          </Text>
        </View>
        <View
          style={{
            flexDirection: "row",
            justifyContent: "space-between",
            borderTopWidth: 1,
            borderColor: "#0001",
          }}
        >
          <View
            style={{
              justifyContent: "space-between",
              alignItems: "center",
              flexDirection: "row",
              flex: 1,
            }}
          >
            <View
              style={{
                flex: 1,
                flexDirection: "row",
              }}
            >
              {item.department_name.map((v, i) => (
                <View
                  key={"departmentName" + i}
                  style={{
                    borderWidth: 1,
                    marginLeft: 2,
                    borderColor: Colors.threeText,
                  }}
                >
                  <Text
                    style={{
                      color: Colors.lightTextColor,

                      fontSize: dH(10),
                      paddingHorizontal: 2,
                      textAlign: "center",
                    }}
                  >
                    {v.name}
                  </Text>
                </View>
              ))}
            </View>
          </View>
        </View>
        <View
          style={{
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          {item.facebookEngagement && (
            <FBLikeButton
              onPress={() => We}
              like={item.facebookEngagement}
            ></FBLikeButton>
          )}
          <Text
            style={{
              color: Colors.lightTextColor,
              fontWeight: "bold",
              fontSize: dH(8),
            }}
          >
            {moment(item.date).fromNow()}
          </Text>
        </View>
      </View>
    </TouchableOpacity>
  );
};

