const DETAIL_FETCHING_DATA = "DETAIL_FETCHING_DATA";
const DETAIL_FETCHING_DATA_FAILURE = "DETAIL_FETCHING_DATA_FAILURE";
const DETAIL_FETCHING_DATA_RESET = "DETAIL_FETCHING_DATA_RESET";
const DETAIL_FETCHING_DATA_SUCCESS = "DETAIL_FETCHING_DATA_SUCCESS";
const DETAIL_FETCHING_DATA_ATTEMPT = "DETAIL_FETCHING_DATA_ATTEMPT";
const UP_VOTE = "UP_VOTE";
const DOWN_VOTE = "DOWN_VOTE";

const getDetailForId = (data, fbToken, email = "", loading = true) => ({
  type: DETAIL_FETCHING_DATA_ATTEMPT,
  payload: data,
  fbToken,
  email,
  loading,
});
const upVote = (payload) => ({
  type: UP_VOTE,
  payload,
});
const downVote = (payload) => ({
  type: DOWN_VOTE,
  payload,
});
export {
  DETAIL_FETCHING_DATA,
  DETAIL_FETCHING_DATA_FAILURE,
  DETAIL_FETCHING_DATA_RESET,
  DETAIL_FETCHING_DATA_SUCCESS,
  DETAIL_FETCHING_DATA_ATTEMPT,
  DOWN_VOTE,
  UP_VOTE,
  getDetailForId,
  upVote,
  downVote,
};
