import React, { Component, Fragment } from "react";
import {
  Text,
  View,
  Image,
  ScrollView,
  FlatList,
  ActivityIndicator,
  Dimensions,
  TouchableOpacity,
  ImageBackground,
  Modal,
  Button,
  KeyboardAvoidingView,
} from "react-native";
import * as Facebook from "expo-facebook";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Ionicons } from "@expo/vector-icons";
import { getDetailForId } from "../Detail/Action";
import _ from "lodash";
import {
  Loading,
  DoubleCard,
  SingleCard,
  FoxTopBar,
  FoxStandardCard,
  FoxMasonaryCard,
  FoxButton,
  FoxAvatar,
  FoxStandardCardTwo,
} from "../../Components";
import * as firebase from "firebase";
import { socialLoginFetchProfile, saveCommment } from "./Action";
import moment from "moment";
import { Foundation, AntDesign, Entypo } from "@expo/vector-icons";
import { Colors, dW, dH, Styles, Images } from "../../Constants";
import { TextInput } from "react-native-gesture-handler";

class Screen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      offset: 0,
      showmenu: true,
      showLogIn: false,
      data: [],
      text: "",
      commentPost: false,
      filter: null,
    };
  }
  async componentDidMount() {
    this.focusListener = this.props.navigation.addListener("didFocus", () => {
      this.onFocusFunction();
    });
  }

  onFocusFunction = async () => {
    const commentPost = this.props.navigation.getParam("chat");
    await this.setState({ commentPost });
  };

  // and don't forget to remove the listener
  componentWillUnmount() {
    this.focusListener.remove();
  }

  isCloseToBottom({ layoutMeasurement, contentOffset, contentSize }) {
    return (
      layoutMeasurement.height + contentOffset.y >= contentSize.height - 20
    );
  }
  isCloseToTop({ layoutMeasurement, contentOffset, contentSize }) {
    return contentOffset.y == 0;
  }
  /**
   * id, date, date_gmt, guid, rendered, modified, modified_gmt, slug, status, type, link, title, rendered, content, rendered, protected, featured_media, comment_status, ping_status, template, _links, self, href, collection, href, about, href, replies, embeddable, href, wp:featuredmedia, embeddable, href, wp:attachment, href, curies, name, href, templated
   */
  render() {
    const { isFetching } = this.props.profile;
    const { data } = this.props.chat;
    return (
      <Fragment>
        <View
          showsVerticalScrollIndicator={false}
          style={{
            height: "100%",
          }}
          ref={(ref) => (this.scrollView = ref)}
          onContentSizeChange={(contentWidth, contentHeight) => {}}
          keyboardShouldPersistTaps="handled"
          showsVerticalScrollIndicator={false}
        >
          <View
            style={[
              Styles.shadowGenerator,
              {
                borderBottomWidth: 4,
                backgroundColor: Colors.five,
                borderColor: "#f0f0f0",
                elevation: 10,
              },
            ]}
          >
            <View style={{ flexDirection: "row" }}>
              <TouchableOpacity
                key={"filter"}
                onPress={() => {
                  this.setState({ filter: false });
                }}
                style={{
                  justifyContent: "center",
                  alignItems: "center",
                  paddingVertical: dH(8),
                  paddingHorizontal: dW(16),
                  flexDirection: "row",
                }}
              >
                <View
                  activeOpacity={0.8}
                  style={{
                    width: dW(30),
                    height: dW(30),
                    backgroundColor: "#fff",
                    padding: 2,
                    borderRadius: dH(32),
                    shadowColor: "#000",
                    shadowOffset: {
                      width: 0,
                      height: 2,
                    },
                    shadowOpacity: 0.25,
                    shadowRadius: 3.84,
                    zIndex: 9999,
                  }}
                >
                  <Image
                    source={Images.logo}
                    style={{
                      flex: 1,
                      width: undefined,
                      height: undefined,
                      borderRadius: dH(30),
                    }}
                  ></Image>
                </View>
              </TouchableOpacity>
              <FlatList
                horizontal
                stickyHeaderIndices={[0]}
                renderItem={({ item, index }) => {
                  return (
                    <>
                      <TouchableOpacity
                        key={"filter" + index}
                        onPress={() => {
                          console.warn(item.id);
                          this.setState({ filter: item.id });
                        }}
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          paddingVertical: dH(8),
                          paddingHorizontal: dW(16),
                          flexDirection: "row",
                        }}
                      >
                        <View
                          activeOpacity={0.8}
                          onPress={() =>
                            this.setState({
                              text: this.state.text + "@" + item.name,
                            })
                          }
                          style={{
                            width: dW(30),
                            height: dW(30),
                            backgroundColor: "#fff",
                            padding: 2,
                            borderRadius: dH(32),
                            shadowColor: "#000",
                            shadowOffset: {
                              width: 0,
                              height: 2,
                            },
                            shadowOpacity: 0.25,
                            shadowRadius: 3.84,
                            zIndex: 9999,
                          }}
                        >
                          <Image
                            source={
                              _.isEmpty(item.img_url)
                                ? Images.logo
                                : { uri: item.img_url }
                            }
                            style={{
                              flex: 1,
                              width: undefined,
                              height: undefined,
                              borderRadius: dH(30),
                            }}
                          ></Image>
                        </View>
                        <Text style={{ fontSize: dH(10), marginHorizontal:dW(8) }}>{item.name}</Text>
                      </TouchableOpacity>
                    </>
                  );
                }}
                data={_.uniqBy(data, "id")}
              />
            </View>
            <View
              style={{
                flexDirection: "row",
                justifyContent: "space-between",
                alignItems: "flex-start",
              }}
            >
              <TextInput
                onChangeText={(text) => {
                  if (text.length < 256) {
                    this.setState({ text });
                  }
                }}
                value={this.state.text}
                placeholderTextColor={"#a0a0a0"}
                placeholder={"Type your message"}
                multiline
                style={{
                  backgroundColor: "#fff",
                  fontSize: dH(16),
                  borderRadius: 3,
                  flex: 1,
                  marginHorizontal: dW(4),
                  height: "100%",
                }}
              ></TextInput>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={async () => {
                  if (_.isEmpty(this.props.profile.email)) {
                    this.props.navigation.navigate("MyStuff");
                  } else {
                    if (!_.isEmpty(this.state.text.trim())) {
                      const timeStamp = await firebase.database.ServerValue
                        .TIMESTAMP;
                      const {
                        email,
                        id,
                        avatar,
                        name,
                      } = this.props.profile.dataInfo[0];
                      const messagesRef = firebase
                        .database()
                        .ref()
                        .child("messages");
                      messagesRef.push({
                        date: timeStamp,
                        img_url: avatar,
                        is_active: true,
                        message: this.state.text,
                        name: name,
                        id: id,
                        user_email: _.isEmpty(email) ? id : email,
                        commentPost: _.isEmpty(this.state.commentPost)
                          ? false
                          : this.state.commentPost,
                      });
                      this.state.commentPost &&
                        this.props.actions.saveCommment({
                          commentedBy: name,
                          commentatorImage: avatar,
                          commentatorId: id,
                          commentatorEmail: email,
                          commentContent: this.state.text,
                          postId: this.state.commentPost.id,
                          password:
                            "akfiou duadsfucasfhaisundc kasdhfoiausfnia dsufadufnaiosudfhoaisu nfoiadfiosdfhoiahfoia",
                          postUrl: this.state.commentPost.link,
                        });
                    }
                    this.setState({ text: "", commentPost: false });
                  }
                }}
                style={{ padding: dW(8) }}
              >
                <Ionicons name="md-send" size={dH(30)} color={Colors.oneText} />
              </TouchableOpacity>
            </View>
            {this.state.commentPost && (
              <View style={{ backgroundColor: "#fff" }}>
                <FoxStandardCardTwo
                  isCommentPost={true}
                  onPress={async () =>
                    await this.setState({ commentPost: false })
                  }
                  userEmail={
                    _.isEmpty(this.props.profile.email)
                      ? null
                      : this.props.profile.email
                  }
                  item={this.state.commentPost}
                  onPressClose={async () =>
                    await this.setState({ commentPost: false })
                  }
                ></FoxStandardCardTwo>
              </View>
            )}
          </View>

          <ScrollView
            keyboardShouldPersistTaps="handled"
            style={{
              flex: 1,
              paddingHorizontal: dW(8),
              backgroundColor: Colors.two,
            }}
          >
            <FlatList
              data={
                this.state.filter
                  ? data.filter((v) => v.id == this.state.filter)
                  : data
              }
              contentContainerStyle={{ paddingBottom: 20 }}
              showsVerticalScrollIndicator={false}
              ref={(ref) => (this.FlatScrollVIew = ref)}
              onContentSizeChange={(contentWidth, contentHeight) => {}}
              keyExtractor={({ index }) => index}
              renderItem={({ item, index }) => {
                const { email, id, avatar, name } = this.props.profile;

                return (
                  <View key={"chatdata" + index}>
                    <View>
                      <View
                        style={[
                          { zIndex: 9999, elevation: 6 },
                          (email == item.user_email ||
                            id == item.user_email) && {
                            alignItems: "flex-end",
                          },
                        ]}
                      >
                        <Text
                          style={{
                            color: "#000",
                            fontSize: dH(10),
                          }}
                        >
                          {item.name}
                        </Text>
                        <TouchableOpacity
                          activeOpacity={0.8}
                          onPress={() =>
                            this.setState({
                              text: this.state.text + "@" + item.name,
                            })
                          }
                          style={{
                            position: "absolute",
                            width: dW(40),
                            height: dW(40),
                            backgroundColor: "#fff",
                            padding: 2,
                            borderRadius: dH(32),
                            shadowColor: "#000",
                            shadowOffset: {
                              width: 0,
                              height: 2,
                            },
                            shadowOpacity: 0.25,
                            shadowRadius: 3.84,
                            zIndex: 9999,
                            elevation: 6,
                            top: "100%",
                          }}
                        >
                          <Image
                            source={
                              _.isEmpty(item.img_url)
                                ? Images.logo
                                : { uri: item.img_url }
                            }
                            style={{
                              flex: 1,
                              width: undefined,
                              height: undefined,
                              borderRadius: dH(30),
                            }}
                          ></Image>
                        </TouchableOpacity>
                      </View>
                      <Text
                        style={{
                          textAlign: "center",
                          fontSize: dH(10),
                          color: "#a0a0a0",
                        }}
                      >
                        {moment(item.date).fromNow()}
                      </Text>
                      {item.commentPost && (
                        <View
                          style={{
                            backgroundColor: "#fff",
                            paddingTop: dH(4),
                          }}
                        >
                          <FoxStandardCardTwo
                            onPress={async () => {
                              this.props.navigation.navigate("Detail");
                              await this.props.actions.getDetailForId(
                                item.commentPost,
                                this.props.profile.logInResponse.token,
                                this.props.profile.email
                              );
                            }}
                            userEmail={
                              _.isEmpty(this.props.profile.email)
                                ? null
                                : this.props.profile.email
                            }
                            item={item.commentPost}
                            isCommentPost={true}
                            onPressReply={() =>
                              this.setState({
                                commentPost: item.commentPost,
                                text: "@" + item.name + " ",
                              })
                            }
                          ></FoxStandardCardTwo>
                        </View>
                      )}
                      <View
                        style={[
                          Styles.panel,
                          {
                            borderRadius: 8,
                            paddingVertical: dH(8),
                            marginBottom: dH(8),
                            marginTop: item.commentPost ? 0 : dH(8),
                            marginHorizontal: dH(8),
                            backgroundColor: Colors.one,
                            width: "80%",
                          },
                          email == item.user_email || id == item.user_email
                            ? {
                                alignSelf: "flex-end",
                                borderBottomEndRadius: 0,
                                paddingRight: dW(32),
                                paddingLeft: dW(8),
                              }
                            : {
                                alignSelf: "flex-start",
                                borderBottomLeftRadius: 0,
                                paddingLeft: dW(32),
                                paddingRight: dW(8),
                              },
                        ]}
                      >
                        <Text
                          style={[
                            { color: "#000", fontSize: dH(16) },
                            email == item.user_email || id == item.user_email
                              ? {
                                  textAlign: "left",
                                }
                              : { textAlign: "right" },
                          ]}
                        >
                          {item.message}
                        </Text>
                      </View>
                    </View>
                  </View>
                );
              }}
            ></FlatList>
          </ScrollView>
        </View>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  profile: state.profile,
  chat: state.chat,
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(
      {
        socialLoginFetchProfile,
        getDetailForId,
        saveCommment,
      },
      dispatch
    ),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Screen);
