import { ToastAndroid, Platform } from "react-native";
const myAPI = new Api();
const getDataAsPerSearchKeywordsEpic = (action$) =>
  action$.pipe(
    ofType(NEWS_BY_KEYWORDS_FETCHING_DATA_ATTEMPT),
    flatMap(async (action) => {
      try{
        const response = await myAPI.getNews(action.payload);

        let payload = get(response, "data");
        of({
          type: NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
          payload: payload,
        });
        payload = payload.map(async (v, i) => {
          try {
            const datasource = await myAPI.getLikes({
              link: v.link,
              token: action.fbToken,
            });
            v.facebookEngagement = intToString(
              datasource.engagement.reaction_count
            );
          } catch (e) {
            v.facebookEngagement = false;
          }
          return v;
        });
        payload = await Promise.all(payload);
        return { payload };
      }catch(e){
        console.log(e,e.response)
        return false;
      }
    }),
    map((action) => {
      if(action){
        return {
          type: NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
          payload: action.payload,
        };
      }else{
        return {
          type: NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
          payload: action.payload,
        };
      }
    })
  );
