const saveCommentEpic = (action$) =>
  action$.pipe(
    ofType(SAVE_COMMENT),
    flatMap(async (action) => {
      try{
      return await myAPI.postComment(action.payload)
    }catch(e){
      return false;
    }
    
    }),
    switchMap((response1) => {
      if(response1){
      let payload1 = get(response1, "data");
      return [
        {
          type: "POST_COMMENT",
          payload: payload1,
        },
      ];}else{
        return [{
          type: "POST_COMMENT_FAILURE"
        }]
      }
    })
  );

const getHeadlinesEpic = (action$) =>
  action$.pipe(
    ofType(GET_HEADLINES),
    flatMap(async (action) => {
      try{
      return await myAPI.getHeadlinesNews()}
    catch(e){
      return false;
    }}),
    switchMap((response1) => {
      if(response1){
      let payload1 = get(response1, "data");
      return [
        {
          type: SET_HEADLINES,
          payload: payload1,
        },
      ];}else{
        return [
          {
            type: NEWS_FETCHING_DATA_FAILURE,
          },
        ];
      }
    })
  );

const aboutFetchingDataEpic = (action$) =>
  action$.pipe(
    ofType(ABOUT_FETCHING_DATA_ATTEMPT),
    flatMap(async (action) => [
      await myAPI.getAbout(action.payload),
      await myAPI.getUsers(),
    ]),
    switchMap(([response1, response2]) => {
      let payload1 = get(response1, "data");
      let payload2 = get(response2, "data");
      return [
        {
          type: ABOUT_FETCHING_DATA_SUCCESS,
          payload: payload1,
        },
        {
          type: USER_DATA_FETCHING_SUCCESS,
          payload: payload2,
        },
      ];
    })
  );

export const rootEpic = combineEpics(
  newsDetailFetchingDataEpic,
  getDataAsPerSearchKeywordsEpic,
  categoryFetchingDataEpic,
  aboutFetchingDataEpic,
  profileFetchingDataEpic,
  newsByCategoryFetchingDataAttempt,
  newsFetchingDataEpic,
  saveCommentEpic,
  upVoteEpic,
  getHeadlinesEpic,
  downVote
);
