const NEWS_FETCHING_DATA = "NEWS_FETCHING_DATA";
const NEWS_FETCHING_DATA_FAILURE = "NEWS_FETCHING_DATA_FAILURE";
const NEWS_FETCHING_DATA_RESET = "NEWS_FETCHING_DATA_RESET";
const NEWS_FETCHING_DATA_SUCCESS = "NEWS_FETCHING_DATA_SUCCESS";
const NEWS_FETCHING_DATA_ATTEMPT = "NEWS_FETCHING_DATA_ATTEMPT";
const NEWS_FETCHING_DATA_CANCEL = "NEWS_FETCHING_DATA_CANCEL";
const SET_CHAT_DATA = "SET_CHAT_DATA";
const HEAD_NEWS_FETCHING_DATA_SUCCESS = "HEAD_NEWS_FETCHING_DATA_SUCCESS";

const CATEGORIES_FETCHING_DATA_SUCCESS = "CATEGORIES_FETCHING_DATA_SUCCESS";
const CATEGORIES_FETCHING_DATA_ATTEMPT = "CATEGORIES_FETCHING_DATA_ATTEMPT";
const GET_HEADLINES = "GET_HEADLINES";
const SET_HEADLINES = "SET_HEADLINES";
const SET_FIRSTTIME = "SET_FIRSTTIME";

const newsFetchingData = (page = 0, token = null) => ({
  type: NEWS_FETCHING_DATA_ATTEMPT,
  payload: page,
  fbToken: token,
});
const getAllCategories = () => ({
  type: CATEGORIES_FETCHING_DATA_ATTEMPT,
});
const setChatData = (payload) => ({
  type: SET_CHAT_DATA,
  payload,
});
const getHeadlines = () => ({
  type: GET_HEADLINES,
});
const setFirstTime = () => ({
  type: SET_FIRSTTIME,
});
export {
  GET_HEADLINES,
  NEWS_FETCHING_DATA,
  SET_FIRSTTIME,
  NEWS_FETCHING_DATA_FAILURE,
  NEWS_FETCHING_DATA_RESET,
  NEWS_FETCHING_DATA_SUCCESS,
  NEWS_FETCHING_DATA_ATTEMPT,
  NEWS_FETCHING_DATA_CANCEL,
  CATEGORIES_FETCHING_DATA_SUCCESS,
  CATEGORIES_FETCHING_DATA_ATTEMPT,
  HEAD_NEWS_FETCHING_DATA_SUCCESS,
  newsFetchingData,
  getAllCategories,
  SET_HEADLINES,
  setFirstTime,
  getHeadlines,
  SET_CHAT_DATA,
  setChatData,
};
