const PROFILE_FETCHING_DATA = "PROFILE_FETCHING_DATA";
const PROFILE_FETCHING_DATA_FAILURE = "PROFILE_FETCHING_DATA_FAILURE";
const PROFILE_FETCHING_DATA_RESET = "PROFILE_FETCHING_DATA_RESET";
const PROFILE_FETCHING_DATA_SUCCESS = "PROFILE_FETCHING_DATA_SUCCESS";
const PROFILE_FETCHING_DATA_ATTEMPT = "PROFILE_FETCHING_DATA_ATTEMPT";
const TOKEN_STORE_FB = "TOKEN_STORE_FB";
const REMOVE_USER = "REMOVE_USER";
const PROFILE_REQUEST_CANCEL = "PROFILE_REQUEST_CANCEL";

const socialLoginFetchProfile = (
  name = "",
  link = "",
  id = null,
  email = null,
  picture = "https://lh3.googleusercontent.com/proxy/cxwrGK6VLf14dSeWrwjhnLTBomtKQAu8TMJhl9SMGasBvqVd2HNGhz-YO8iUK0BWl3Ytj51FG-u1QHhiExtG_uahgNNmPZW3b16dUAuQ6nZGpxanK17bQCuD5DQhfzOiQos",
  site = "",
  logInResponse = {}
) => ({
  type: PROFILE_FETCHING_DATA_ATTEMPT,
  payload: { name, link, site, logInResponse, id, email, picture },
});
const saveFBToken = (t) => ({
  type: TOKEN_STORE_FB,
  payload: t,
});
const removeUser = (item) => ({
  type: REMOVE_USER,
  payload: item,
});
const cancelProfileRequest = () => ({
  type: PROFILE_REQUEST_CANCEL,
});
export {
  removeUser,
  REMOVE_USER,
  TOKEN_STORE_FB,
  PROFILE_FETCHING_DATA,
  PROFILE_FETCHING_DATA_FAILURE,
  PROFILE_REQUEST_CANCEL,
  PROFILE_FETCHING_DATA_RESET,
  PROFILE_FETCHING_DATA_SUCCESS,
  PROFILE_FETCHING_DATA_ATTEMPT,
  socialLoginFetchProfile,
  cancelProfileRequest,
  saveFBToken,
};
