// reducers
import {
  NEWS_BY_CATEGORY_FETCHING_DATA,
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_RESET,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
  NEWS_BY_CATEGORY_FETCHING_DATA_ATTEMPT,
} from "./Action";
import { removeDuplicates } from "../../Constants";
let initialState = {
  page: 0,
  response: [],
  isError: false,
  isFetching: false,
  departmentId: 58,
};

export default function Reducer(state = initialState, action) {
  switch (action.type) {
    case NEWS_BY_CATEGORY_FETCHING_DATA_ATTEMPT:
      return {
        ...state,
        isFetching: true,
        isError: false,
      };
    case NEWS_BY_CATEGORY_FETCHING_DATA:
      return {
        ...state,
        isFetching: true,
        isError: false,
      };
    case NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
        isError: action.payload,
      };
    case NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS:
      return action.fresh
        ? {
            ...state,
            isFetching: false,
            isError: false,
            response: removeDuplicates(
              (thing1, thing2) => thing1.id === thing2.id,
              [...state.response, ...action.payload]
            ),
            page: state.page + 1,
            departmentId: action.departmentId,
          }
        : {
            ...state,
            isFetching: false,
            isError: false,
            response: action.payload,
            page: 0,
            departmentId: action.departmentId,
          };
    case NEWS_BY_CATEGORY_FETCHING_DATA_RESET:
      return initialState;
    default:
      return state;
  }
}
