import { combineReducers } from "redux";
import HomeReducer from "../Screens/Home/Reducer";
import SearchReducer from "../Screens/Search/Reducer";
import ProfileReducer from "../Screens/Profile/Reducer";
import DetailReducer from "../Screens/Detail/Reducer";
import AboutReducer from "../Screens/About/Reducer";
import ChatReducer from "../Screens/Chat/Reducer";

const rootReducer = combineReducers({
  blank: (state = null) => state,
  home: HomeReducer,
  search: SearchReducer,
  profile: ProfileReducer,
  detail: DetailReducer,
  about: AboutReducer,
  chat: ChatReducer,
});

export default rootReducer;
