import React, { Component } from "react";
import {
  View,
  ImageBackground,
  Dimensions,
  SafeAreaView,
  Text,
  TouchableOpacity,
  Modal,
  Image,
  Platform,
} from "react-native";
import { connect } from "react-redux";
import { Notifications } from "expo";
import * as Permissions from "expo-permissions";
import { getHeadlines, setFirstTime } from "../Screens/Home/Action";
import NavigationService from "./NavigationService";
import MainNavigator from "./MainNavigator";
import { Images, Styles, Colors, dH,dW } from "../Constants";
import { Foundation, FontAwesome } from "@expo/vector-icons";
import { Entypo } from "@expo/vector-icons";
import * as Speech from "expo-speech";
import { bindActionCreators } from "redux";
// const PUSH_ENDPOINT = "https://your-server.com/users/push-token";
class AppNavigator extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSpeakingAsync: false,
      show: false,
    };
  }
  async registerForPushNotificationsAsync() {
    const { status } = await Permissions.getAsync(Permissions.NOTIFICATIONS);
    let finalStatus = status;
    if (status !== "granted") {
      const { status } = await Permissions.askAsync(Permissions.NOTIFICATIONS);
      finalStatus = status;
    }
    if (finalStatus !== "granted") {
      return;
    }
    let token = await Notifications.getExpoPushTokenAsync();
    Notifications.addNotificationReceivedListener(() => console.warn("asdfadf"));
    
    Notifications.addNotificationResponseReceivedListener(() => console.warn("asdfadf"));
  }
  componentDidMount = async () => {
    await this.registerForPushNotificationsAsync();
    await this.props.actions.getHeadlines();
  };

  render() {
    return (
      <View style={{ flex: 1 }}>
        <>
          <MainNavigator
            ref={(navigatorRef) => {
              NavigationService.setTopLevelNavigator(navigatorRef);
            }}
          />
          <View style={{position: "absolute",
                zIndex: 9990,
                flexDirection:"row",
                top:dH(45),
                alignSelf:"flex-end",}}>
          {this.state.show && (
            <View
              style={[
                Styles.shadowGenerator,
                {
                  elevation: 2,
                  height: dH(45),
                  zIndex: 9990,
                  alignItems: "center",
                  justifyContent: "center",
                  flexDirection: "row",
                  
                },
              ]}
            >
              <TouchableOpacity
                onPress={() => {
                  const data = this.props.home.highLights
                    .map((v, i) => v.title.rendered)
                    .join(" ")
                    .replace(/(<([^>]+)>)/gi, "")
                    .replace(/(\r\n|\n|\r)/gm, " ")
                    .trim();
                  Speech.speak("  ______________" + data, {
                    language: Platform.OS == "ios" ? "hi-IN" : "ne_NP",
                  });
                }}
                style={[
                  {
                    padding: dW(8),
                    borderWidth: 1,
                    borderColor: "#f0f0f0",
                    borderRadius: dH(20),
                    width: dH(35),
                    height: dH(35),
                    justifyContent: "center",
                    alignItems: "center",
                    marginHorizontal: dW(4),
                    backgroundColor: "#fff",
                  },
                  Styles.shadowGenerator,
                ]}
              >
                <FontAwesome name="play" size={dH(16)} color="black" />
              </TouchableOpacity>
              <TouchableOpacity
                onPress={() => Speech.stop()}
                style={[
                  {
                    padding: dW(8),
                    borderWidth: 1,
                    borderColor: "#f0f0f0",
                    borderRadius: dH(20),
                    width: dH(35),
                    height: dH(35),
                    justifyContent: "center",
                    alignItems: "center",
                    marginHorizontal: dW(4),
                    backgroundColor: "#fff",
                  },
                  Styles.shadowGenerator,
                ]}
              >
                <FontAwesome name="stop" size={dH(16)} color="black" />
              </TouchableOpacity>
            </View>
          )}
          <TouchableOpacity
            onPress={async () => {
              this.setState({ show: !this.state.show });
            }}
            style={[
              {
                backgroundColor: "#fff",
                width: dH(35),
                height: dH(45),
                elevation: 3,
                borderWidth: 1,
                borderColor: "#d0d0d0",
                justifyContent: "center",
                alignItems: "center",
              },
            ]}
          >
            <Entypo
              name={this.state.show ? "align-right" : "align-left"}
              size={dH(24)}
              color="black"
            />
          </TouchableOpacity></View>
        </>
      </View>
    );
  }
}

const mapStateToProps = (state) => ({
  home: state.home,
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(
      {
        getHeadlines,
        setFirstTime,
      },
      dispatch
    ),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AppNavigator);
