import { Dimensions } from "react-native";

const ValidEmail = (email) => {
  const checker = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/; // eslint-disable-line
  return checker.test(email);
};
function decodeHTMLEntities(text) {
  var entities = [
    ["amp", "&"],
    ["apos", "'"],
    ["#x27", "'"],
    ["#x2F", "/"],
    ["#39", "'"],
    ["#47", "/"],
    ["lt", "<"],
    ["gt", ">"],
    ["nbsp", " "],
    ["quot", '"'],
    ["bull", ""],
    ["rsquo", "'"],
    ["#8220", ""],
    ["#8221", ""],
    ["#8230", "."],
  ];

  for (var i = 0, max = entities.length; i < max; ++i)
    text = text.replace(
      new RegExp("&" + entities[i][0] + ";", "g"),
      entities[i][1]
    );

  return text;
}
const validFirstName = (first_name) => {
  const checker = /^[a-z ,.'-]+$/i;
  return checker.test(first_name);
};
const validCode = (code) => {
  return code.length >= 4;
};
const validLastName = (last_name) => {
  const checker = /^[a-z ,.'-]+$/i;
  return checker.test(last_name);
};
const validNumber = (number) => {
  const checker = /^\d*$/;
  return checker.test(number);
};
const validPassword = (pass) => {
  return pass.length >= 8;
};
const validInternationalPhoneNumber = (phone) => {
  const checker = /(^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]{7,20})|(^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]{7,20})$/;
  return checker.test(phone);
};
const validAustraliaNumber = (phone) => {
  const checker = /^(?:\+?(61))? ?(?:\((?=.*\)))?(0?[2-57-8])\)? ?(\d\d(?:[- ](?=\d{3})|(?!\d\d[- ]?\d[- ]))\d\d[- ]?\d[- ]?\d{3})$/;
  return checker.test(phone);
};
function intToString(value) {
  var suffixes = ["", "k", "m", "b", "t"];
  var suffixNum = Math.floor(("" + value).length / 3);
  var shortValue = parseFloat(
    (suffixNum != 0 ? value / Math.pow(1000, suffixNum) : value).toPrecision(2)
  );
  if (shortValue % 1 != 0) {
    shortValue = shortValue.toFixed(1);
  }
  return shortValue + suffixes[suffixNum];
}
const getRegionForCoordinates = (points) => {
  // points should be an array of { latitude: X, longitude: Y }
  let minX, maxX, minY, maxY;

  // init first point
  ((point) => {
    minX = point.latitude;
    maxX = point.latitude;
    minY = point.longitude;
    maxY = point.longitude;
  })(points[0]);

  // calculate rect
  points.map((point) => {
    minX = Math.min(minX, point.latitude);
    maxX = Math.max(maxX, point.latitude);
    minY = Math.min(minY, point.longitude);
    maxY = Math.max(maxY, point.longitude);
  });

  const midX = (minX + maxX) / 2;
  const midY = (minY + maxY) / 2;
  const deltaX = maxX - minX;
  const deltaY = maxY - minY;

  return {
    latitude: midX,
    longitude: midY,
    latitudeDelta: deltaX,
    longitudeDelta: deltaY,
  };
};
function removeDuplicates(equals, arr) {
  var originalArr = arr.slice(0);
  var i, len, val;
  arr.length = 0;

  for (i = 0, len = originalArr.length; i < len; ++i) {
    val = originalArr[i];
    if (
      !arr.some(function (item) {
        return equals(item, val);
      })
    ) {
      arr.push(val);
    }
  }
  return arr;
}
const validZipCode = (zipcode) => {
  const checker = /^(\d{2,5}((|-)-\d{4})?)|([A-Za-z]\d[A-Za-z][\s\.\-]?(|-)\d[A-Za-z]\d)|[A-Za-z]{1,2}\d{1,2}[A-Za-z]? \d[A-Za-z]{2}$/;
  return checker.test(zipcode);
};

const { width, height } = Dimensions.get("window");

const guidelineBaseWidth = 350;
const guidelineBaseHeight = 680;

const Scale = (size) => (width / guidelineBaseWidth) * size;
const VerticalScale = (size) => (height / guidelineBaseHeight) * size;
const ModerateScale = (size, factor = 0.5) =>
  size + (Scale(size) - size) * factor;

export {
  ValidEmail,
  validFirstName,
  validLastName,
  Scale,
  VerticalScale,
  ModerateScale,
  validNumber,
  validInternationalPhoneNumber,
  getRegionForCoordinates,
  validZipCode,
  validAustraliaNumber,
  validPassword,
  validCode,
  removeDuplicates,
  intToString,
  decodeHTMLEntities,
};
