// reducers
import {
  NEWS_FETCHING_DATA,
  NEWS_FETCHING_DATA_FAILURE,
  NEWS_FETCHING_DATA_RESET,
  NEWS_FETCHING_DATA_SUCCESS,
  NEWS_FETCHING_DATA_ATTEMPT,
  CATEGORIES_FETCHING_DATA_SUCCESS,
  CATEGORIES_FETCHING_DATA_ATTEMPT,
  SET_HEADLINES,
  SET_FIRSTTIME,
} from "./Action";
import {
  DETAIL_FETCHING_DATA_ATTEMPT,
  DETAIL_FETCHING_DATA_FAILURE,
  DETAIL_FETCHING_DATA_SUCCESS,
} from "../Detail/Action";
import {
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
} from "../Search/Action";
let initialState = {
  page: 0,
  response: [],
  departmentResponse: [],
  isError: false,
  isFetching: false,
  highLights: [],
  firstTime: true,

  // response: [],
  // links:{
  //     current_page: 0,
  //     total_pages: null,
  //     total_products_count: null,
  //     per_page: null
  // }
};

export default function Reducer(state = initialState, action) {
  switch (action.type) {
    case SET_FIRSTTIME:
      return {
        ...state,
        firstTime: false,
      };
    case NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
      };

    case NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
      };
    case NEWS_FETCHING_DATA:
      return {
        ...state,
        isFetching: true,
        isError: false,
      };
    case NEWS_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
        isError: action.payload,
      };
    case NEWS_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
        isError: false,
        response: action.payload,
        page: state.page + 1,
      };
    case CATEGORIES_FETCHING_DATA_ATTEMPT:
      return {
        ...state,
        isFetching: true,
        isError: false,
      };
    case CATEGORIES_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
        isError: false,
        departmentResponse: action.payload,
      };
    case NEWS_FETCHING_DATA_RESET:
      return initialState;
    case SET_HEADLINES:
      return {
        ...state,
        highLights: action.payload,
      };
    default:
      return state;
  }
}
