// reducers
import {
  ABOUT_FETCHING_DATA,
  ABOUT_FETCHING_DATA_FAILURE,
  ABOUT_FETCHING_DATA_RESET,
  ABOUT_FETCHING_DATA_SUCCESS,
  USER_DATA_FETCHING_SUCCESS,
} from "./Action";
import {
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
} from "../Search/Action";
let initialState = {
  response: {},
  users: [1, 2, 3, 4, 5],
  isError: false,
  isFetching: false,
  users: [],
};

export default function Reducer(state = initialState, action) {
  switch (action.type) {
    case NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
      };

    case NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
      };
    case ABOUT_FETCHING_DATA:
      return {
        ...state,
        response: {},
        isFetching: true,
        isError: false,
      };
    case ABOUT_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
        isError: action.payload,
      };
    case ABOUT_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
        isError: false,
        response: action.payload,
      };
    case USER_DATA_FETCHING_SUCCESS:
      return {
        ...state,
        users: action.payload,
      };

    case ABOUT_FETCHING_DATA_RESET:
      return initialState;
    default:
      return state;
  }
}
