// reducers
import {
  DETAIL_FETCHING_DATA,
  DETAIL_FETCHING_DATA_FAILURE,
  DETAIL_FETCHING_DATA_RESET,
  DETAIL_FETCHING_DATA_SUCCESS,
  DETAIL_FETCHING_DATA_ATTEMPT,
} from "./Action";
import {
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
} from "../Search/Action";
let initialState = {
  response: {},
  isError: false,
  isFetching: false,
};

export default function Reducer(state = initialState, action) {
  switch (action.type) {
    case NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
      };

    case NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
      };
    case DETAIL_FETCHING_DATA_ATTEMPT:
      return {
        ...state,
        response: action.loading ? action.payload : state.response,
        isFetching: action.loading,
        isError: false,
      };
    case DETAIL_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
        isError: action.payload,
      };
    case DETAIL_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
        isError: false,
        response: action.payload,
      };
    case DETAIL_FETCHING_DATA_RESET:
      return initialState;
    default:
      return state;
  }
}
