import React, { Component, Fragment } from "react";
import {
  Text,
  View,
  Image,
  ScrollView,
  FlatList,
  ActivityIndicator,
  Dimensions,
  SafeAreaView,
} from "react-native";
import { connect } from "react-redux";
import { TouchableOpacity, TextInput } from "react-native-gesture-handler";
import { bindActionCreators } from "redux";
import { Ionicons } from "@expo/vector-icons";
import _ from "lodash";
import {
  Loading,
  DoubleCard,
  SingleCard,
  FoxTopBar,
  FoxStandardCard,
  FoxMasonaryCard,
  SearchBar,
} from "../../Components";
import {
  newsByCategoryFetchingData,
  getDataAsPerSearchKeywords,
} from "./Action";
import { getDetailForId } from "../Detail/Action";
import moment from "moment";
import { Foundation } from "@expo/vector-icons";
import { Colors, dH,dW } from "../../Constants";

class HomeScreen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      offset: 0,
      showmenu: true,
      search: "",
    };
  }
  isCloseToBottom({ layoutMeasurement, contentOffset, contentSize }) {
    return (
      layoutMeasurement.height + contentOffset.y >= contentSize.height - 20
    );
  }
  isCloseToTop({ layoutMeasurement, contentOffset, contentSize }) {
    return contentOffset.y == 0;
  }
  /**
   * id, date, date_gmt, guid, rendered, modified, modified_gmt, slug, status, type, link, title, rendered, content, rendered, protected, featured_media, comment_status, ping_status, template, _links, self, href, collection, href, about, href, replies, embeddable, href, wp:featuredmedia, embeddable, href, wp:attachment, href, curies, name, href, templated
   */
  render() {
    const { response, isFetching, page, departmentId } = this.props.search;
    return (
      <Fragment>
        <ScrollView
          showsVerticalScrollIndicator={false}
          contentContainerStyle={{
            paddingHorizontal: dH(8),
            backgroundColor: Colors.five,
            flex: 1,
          }}
        >
          <SearchBar
            onChangeText={(search) => this.setState({ search })}
            value={this.state.search}
            onSubmitEditing={() =>
              this.props.actions.getDataAsPerSearchKeywords(this.state)
            }
            cross={this.state.search == "" ? false : true}
            onPressCross={() => this.setState({ search: "" })}
          ></SearchBar>
          {!_.isEmpty(response) ? (
            <View>
              <FlatList
                onScroll={async ({ nativeEvent }) => {
                  if (this.isCloseToTop(nativeEvent)) {
                  }
                  if (this.isCloseToBottom(nativeEvent)) {
                    if (!isFetching) {
                      await this.props.actions.newsByCategoryFetchingData({
                        page: page,
                        departmentId: departmentId,
                        fresh: true,
                        token: false,
                      });
                    }
                  }
                }}
                data={response}
                renderItem={({ item, index }) => {
                  return (
                    <View key={"response" + index} style={{ width: "100%" }}>
                      <FoxStandardCard
                        userEmail={
                          _.isEmpty(this.props.profile.email)
                            ? null
                            : this.props.profile.email
                        }
                        onPress={async () => {
                          this.props.navigation.navigate("Detail");
                          await this.props.actions.getDetailForId(
                            item,
                            this.props.profile.logInResponse.token,
                            this.props.profile.email
                          );
                        }}
                        item={item}
                        contentLength={200}
                        index={index}
                      ></FoxStandardCard>
                    </View>
                  );
                }}
                keyExtractor={(item) => item.index}
              ></FlatList>
            </View>
          ) : (
            <ScrollView
              showsVerticalScrollIndicator={false}
              contentContainerStyle={{
                flexDirection: "row",
                flexWrap: "wrap",
                justifyContent: "center",
                alignItems: "center",
                flex: 1,
              }}
            >
              {this.props.home.departmentResponse.map((item, i) => (
                <View
                  key={"depart" + i}
                  style={{
                    padding: dW(8),
                    width: "33%",
                    height:dH( Dimensions.get("screen").width / 4),
                    backgroundColor: Colors.one,
                  }}
                >
                  <TouchableOpacity
                    activeOpacity={0.8}
                    onPress={async () => {
                      const profile = this.props.profile;
                      if (!_.isEmpty(profile.logInResponse)) {
                        await this.props.actions.newsByCategoryFetchingData({
                          page: 0,
                          departmentId: item.id,
                          fresh: false,
                          token: profile.logInResponse.token,
                        });
                      } else {
                        await this.props.actions.newsByCategoryFetchingData({
                          page: 0,
                          departmentId: item.id,
                          fresh: false,
                          token: false,
                        });
                      }

                      this.props.navigation.navigate("Search");
                    }}
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      borderWidth: 1,
                      borderColor: Colors.primary,
                      width: "100%",
                      height: "100%",
                      backgroundColor: Colors.one,
                    }}
                  >
                    <Text
                      style={{
                        color: Colors.oneText,
                        fontWeight: "bold",
                        fontSize: dH(16),
                        textAlign: "center",
                      }}
                    >
                      {item.name.split("-").join("\r\n")}
                    </Text>
                  </TouchableOpacity>
                </View>
              ))}
            </ScrollView>
          )}
        </ScrollView>
        {/*
        {this.state.showmenu && (
          <FoxTopBar
            onPressMenu={() => this.props.navigation.openDrawer()}
          ></FoxTopBar>
        )}
        */}
        <SafeAreaView style={{ backgroundColor: Colors.five }}></SafeAreaView>
        {isFetching && <Loading></Loading>}
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  search: state.search,
  profile: state.profile,
  home: state.home,
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(
      {
        newsByCategoryFetchingData,
        getDataAsPerSearchKeywords,
        getDetailForId,
      },
      dispatch
    ),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeScreen);
