  async createTokenWithCode(code) {
    // 2
    const url =
      `https://github.com/login/oauth/access_token` +
      `?client_id=${github.id}` +
      `&client_secret=${github.secret}` +
      `&code=${code}`;
    // 3
    const res = await fetch(url, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    });
    return res.json();
  }
  async createTokenWithCodeForMS(code, msInfo) {
    // 2
    const url =
      `https://login.microsoftonline.com/${msInfo.tenant}` +
      `?client_id=${msInfo.client_id}` +
      `&client_secret=${msInfo.secret}` +
      `&code=${code}`;
    // 3
    const res = await fetch(url, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    });
    return res.json();
  }
  authUrlWithId(id, fields) {
    return (
      `https://github.com/login/oauth/authorize` +
      `?client_id=${id}` +
      `&redirect_uri=${encodeURIComponent(AuthSession.getRedirectUrl())}` +
      // 3
      `&scope=${encodeURIComponent(fields.join(" "))}`
    );
  }
  linkedInAuthUrlWithId(linkedIn, fields, redirectURL) {
    return `https://www.linkedin.com/oauth/v2/authorization?response_type=code&client_id=${linkedIn.id}&redirect_uri=${redirectURL}&state=fooobar&scope=r_liteprofile%20r_emailaddress`;
  }
