// custom component to display in the drawer

import React from "react";
import {
  View,
  Text,
  ScrollView,
  Image,
  TouchableHighlight,
  StyleSheet,
} from "react-native";
import { connect } from "react-redux";
import { NavigationActions, SafeAreaView } from "react-navigation";
import { isEmpty } from "lodash";
import { newsByCategoryFetchingData } from "../Screens/Search/Action";
import { dH, dW, Colors, Images } from "../Constants";
import { ImageComponent, Loading } from "../Components";
import { TouchableOpacity, FlatList } from "react-native-gesture-handler";
import { bindActionCreators } from "redux";
import { AntDesign } from "@expo/vector-icons";
import _ from "lodash";
class CustomDrawerComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showCategory: true,
      showLinks: true,
      showProfile: true,
    };
  }
  render() {
    const { departmentResponse } = this.props.home;
    const profile = this.props.profile;
    const { dataInfo } = this.props.profile;
    const loginData = dataInfo;
    const anchorLinks = [
      {
        title: "About Us",
        IconType: AntDesign,
        iconName: "infocirlce",
        route: "About",
      },
    ];
    return (
      <ScrollView
        showsVerticalScrollIndicator={false}
        contentContainerStyle={{
          backgroundColor: "#fff",
          padding: dW(8),
        }}
        stickyHeaderIndices={[0, 2, 4, 6]}
      >
        <View
          style={{
            borderBottomWidth: 1,
            borderColor: "#0003",
          }}
        >
          <View
            style={{
              justifyContent: "space-between",
              flexDirection: "row",
              alignItems: "center",
              backgroundColor: "#fff",
            }}
          >
            <Text style={{ fontSize: dH(12) }}>Categories</Text>
            <TouchableOpacity
              activeOpacity={0.8}
              style={{ paddingHorizontal: dW(16), paddingVertical: dH(8) }}
              onPress={() => {
                this.setState({ showCategory: !this.state.showCategory });
              }}
            >
              {!this.state.showCategory ? (
                <AntDesign name="down" size={dH(16)} color="black" />
              ) : (
                <AntDesign name="up" size={dH(16)} color="black" />
              )}
            </TouchableOpacity>
          </View>
        </View>
        <View>
          {this.state.showCategory &&
            departmentResponse.map((v, i) => (
              <TouchableOpacity
                key={"departmentRes" + i}
                activeOpacity={0.8}
                onPress={() => {
                  this.props.navigation.navigate("Search");
                  this.props.actions.newsByCategoryFetchingData({
                    page: 0,
                    departmentId: v.id,
                    fresh: false,
                    token: profile.logInResponse.token,
                  });
                }}
                style={{ paddingTop: 8 }}
              >
                <Text
                  style={{
                    color: "#000",
                    fontWeight: "bold",
                    fontSize: dH(12),
                  }}
                >
                  {v.name.split("-").join("\r\n")}
                </Text>
              </TouchableOpacity>
            ))}
        </View>

        <View
          style={{
            borderBottomWidth: 1,
            borderColor: "#0003",
          }}
        >
          <View
            style={{
              justifyContent: "space-between",
              flexDirection: "row",
              alignItems: "center",
              backgroundColor: "#fff",
            }}
          >
            <Text style={{ fontSize: dH(12) }}>Menu</Text>
            <TouchableOpacity
              activeOpacity={0.8}
              style={{ paddingHorizontal: dW(16), paddingVertical: dH(8) }}
              onPress={() =>
                this.setState({ showLinks: !this.state.showLinks })
              }
            >
              {!this.state.showLinks ? (
                <AntDesign name="down" size={dH(16)} color="black" />
              ) : (
                <AntDesign name="up" size={dH(16)} color="black" />
              )}
            </TouchableOpacity>
          </View>
        </View>
        <View>
          {this.state.showLinks &&
            anchorLinks.map((v, i) => (
              <TouchableOpacity
                key={"anchor" + i}
                onPress={() => this.props.navigation.navigate(v.route)}
                style={{ paddingTop: 8, flexDirection: "row" }}
              >
                <View style={{ paddingRight: 8 }}>
                  <v.IconType
                    name={v.iconName}
                    size={dH(16)}
                    color={"#000"}
                  ></v.IconType>
                </View>
                <Text
                  style={{
                    color: "#000",
                    fontWeight: "bold",
                    fontSize: dH(12),
                  }}
                >
                  {v.title}
                </Text>
              </TouchableOpacity>
            ))}
        </View>

        {!_.isEmpty(loginData) && (
          <View
            style={{
              borderBottomWidth: 1,
              borderColor: "#0003",
            }}
          >
            <View
              style={{
                justifyContent: "space-between",
                flexDirection: "row",
                alignItems: "center",
                backgroundColor: "#fff",
              }}
            >
              <Text style={{ fontSize: dH(12) }}>Logged In As</Text>
              <TouchableOpacity
                activeOpacity={0.8}
                style={{ paddingHorizontal: dW(16), paddingVertical: dH(8) }}
                onPress={() =>
                  this.setState({ showProfile: !this.state.showProfile })
                }
              >
                {!this.state.showProfile ? (
                  <AntDesign name="down" size={dH(16)} color="black" />
                ) : (
                  <AntDesign name="up" size={dH(16)} color="black" />
                )}
              </TouchableOpacity>
            </View>
          </View>
        )}
        <View>
          {this.state.showProfile &&
            loginData.map((item, i) => (
              <View
                key={"loginData" + i}
                style={{
                  paddingTop: 8,
                  flexDirection: "row",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <View style={{ paddingRight: 8 }}>
                  <Image
                    source={{ uri: item.avatar }}
                    style={{ width: dH(20), height: dH(20) }}
                  ></Image>
                </View>
                <View style={{ flex: 1 }}>
                  <Text style={{ fontSize: dH(12) }}>
                    {item.email.replace("@", "\n@")}
                  </Text>
                </View>
              </View>
            ))}
        </View>
        <View
          style={{
            width: "100%",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <View
            activeOpacity={0.8}
            style={{
              width: dH(100),
              height: dH(100),
              borderRadius: dH(100),
              justifyContent: "center",
              backgroundColor: Colors.primary,
              padding: dH(5),
              margin: dH(10),
            }}
          >
            <Image
              source={Images.logo}
              resizeMode="stretch"
              style={{
                flex: 1,
                tintColor: "#0007",
                width: undefined,
                height: undefined,
                borderRadius: dH(100),
              }}
            ></Image>
          </View>
        </View>
      </ScrollView>
    );
  }
}

const styles = StyleSheet.create({
  cardStyle: {
    height: dH(240),
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: Colors.color_F0F3E8,
  },
  nameWrapper: {
    paddingLeft: dW(8),
    alignItems: "center",
  },
  nameStyle: {
    fontSize: dH(20),
    color: Colors.color_606060,
    fontWeight: "bold",
  },
  wrapper: {
    flex: 1,
    height: dH(400),
    paddingVertical: dH(10),
    backgroundColor: Colors.color_FFFFFF,
    paddingBottom: dH(40),
  },
  drawerTextWrapper: {
    flexDirection: "row",
    alignItems: "center",
    height: dH(40),
    width: "100%",
    marginTop: dH(8),
    // paddingLeft:dW(30)
  },
  drawerText: {
    color: Colors.color_707070,
    fontSize: dH(18),
    marginLeft: dW(8),
  },
  imageWrapper: {
    height: dH(25),
    width: dW(25),
  },
  profileWrapper: {
    // marginTop:dH(8),
    height: dH(25),
    width: dW(90),
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 5,
    backgroundColor: Colors.color_80BB01,
    opacity: 0.25,
  },
  profileStyle: {
    fontSize: dH(12),
    color: Colors.color_80BB01,
  },
});

const mapStateToProps = (state) => ({
  home: state.home,
  profile: state.profile,
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(
      {
        newsByCategoryFetchingData,
      },
      dispatch
    ),
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CustomDrawerComponent);
