const NEWS_BY_CATEGORY_FETCHING_DATA = "NEWS_BY_CATEGORY_FETCHING_DATA";
const NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE =
  "NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE";
const NEWS_BY_CATEGORY_FETCHING_DATA_RESET =
  "NEWS_BY_CATEGORY_FETCHING_DATA_RESET";
const NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS =
  "NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS";
const NEWS_BY_CATEGORY_FETCHING_DATA_ATTEMPT =
  "NEWS_BY_CATEGORY_FETCHING_DATA_ATTEMPT";
const NEWS_BY_KEYWORDS_FETCHING_DATA_ATTEMPT =
  "NEWS_BY_KEYWORDS_FETCHING_DATA_ATTEMPT";

const newsByCategoryFetchingData = ({
  page = 0,
  departmentId = 36,
  fresh = false,
  token = false,
}) => ({
  type: NEWS_BY_CATEGORY_FETCHING_DATA_ATTEMPT,
  payload: { page, departmentId },
  fresh,
  fbToken: token,
});
const getDataAsPerSearchKeywords = ({
  page = 0,
  search = "",
  fresh = false,
  token = false,
}) => ({
  type: NEWS_BY_KEYWORDS_FETCHING_DATA_ATTEMPT,
  payload: { search },
  fresh,
  fbToken: token,
});
export {
  NEWS_BY_CATEGORY_FETCHING_DATA,
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_RESET,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
  NEWS_BY_CATEGORY_FETCHING_DATA_ATTEMPT,
  NEWS_BY_KEYWORDS_FETCHING_DATA_ATTEMPT,
  newsByCategoryFetchingData,
  getDataAsPerSearchKeywords,
};
