// reducers
import { SET_CHAT_DATA } from "../Home/Action";
import {
  NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE,
  NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS,
} from "../Search/Action";
let initialState = {
  data: [],
  isError: false,
  isFetching: false,
};

export default function Reducer(state = initialState, action) {
  switch (action.type) {
    case SET_CHAT_DATA:
      return {
        ...state,
        data: [...action.payload, ...state.data],
        isError: false,
        isFetching: false,
      };
    case NEWS_BY_CATEGORY_FETCHING_DATA_FAILURE:
      return {
        ...state,
        isFetching: false,
      };

    case NEWS_BY_CATEGORY_FETCHING_DATA_SUCCESS:
      return {
        ...state,
        isFetching: false,
      };
    default:
      return state;
  }
}
